import React from 'react'
import css from './contact.module.scss'

export default function Contact({contacts}) {
  return (
    <div className={css.contact}>
      {contacts.map(contact => (
        <a 
          href={contact.link}
          style={{backgroundImage: `url(${contact.icon})`}}
          target='_blank'
        ></a>
      ))}
    </div>
  )
}
